<template>
    <v-app>
        <v-app-bar app dense elevation="1">
            <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>

            <v-toolbar-title>
                <h5>{{ app_title }}</h5>
            </v-toolbar-title>

            <v-spacer></v-spacer>
            <v-tooltip bottom color="primary">
                <template v-slot:activator="{ on, attrs }">
                    <v-btn icon @click="logout" v-bind="attrs" v-on="on">
                        <v-icon>mdi-login</v-icon>
                    </v-btn>
                </template>
                <span>Log Out</span>
            </v-tooltip>
            <v-btn icon>
                <v-icon>mdi-cog-outline</v-icon>
            </v-btn>
            <v-btn icon>
                <v-icon>mdi-dots-vertical</v-icon>
            </v-btn>
        </v-app-bar>

        <v-navigation-drawer app v-model="drawer" :fixed="$vuetify.breakpoint.mdAndUp">
            <v-list-item>
                <v-list-item-content>
                    <v-list-item-title class="font-weight-black">
                        {{ app_title }}
                    </v-list-item-title>
                    <v-list-item-subtitle class="caption">
                        {{ version }}
                    </v-list-item-subtitle>
                </v-list-item-content>
            </v-list-item>

            <v-divider></v-divider>

            <v-list dense nav>
                <v-list-item-group v-model="selectedItem" color="primary">
                    <v-list-item v-for="(item, i) in items" :key="i" :to="item.to" link>
                        <v-list-item-icon>
                            <v-icon>{{ item.icon }}</v-icon>
                        </v-list-item-icon>

                        <v-list-item-content>
                            <v-list-item-title>{{ item.title }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list-item-group>
            </v-list>
        </v-navigation-drawer>

        <!-- Sizes your content based upon application components -->
        <v-main v-resize="onResize" class="pa-0">
            <!-- Provides the application the proper gutter -->
            <v-container class="grey lighten-5 pa-0">
                <!-- If using vue-router -->
                <router-view></router-view>
            </v-container>
        </v-main>

        <v-footer app>
            <div class="caption indigo--text text--darken-2">
                {{ windowSize }},{{ breakepointName }}
            </div>
        </v-footer>
    </v-app>
</template>
<script>

export default {
    data: () => ({
        app_title: ' ',
        windowSize: {
            x: 0,
            y: 0,
        },        
        selectedItem: 1,
        drawer: false,
        version: 'version 0.2',
        items: [
            {
                icon: 'mdi-alpha-a-box-outline',
                title: '  #1',
                to: '/',
            },
            {
                icon: 'mdi-alpha-b-box-outline',
                title: '  #2',
                to: '/one',
            },
            {
                icon: 'mdi-alpha-c-box-outline',
                title: '  #3',
                to: '/two',
            },
            {
                icon: 'mdi-alpha-d-box-outline',
                title: '  #4',
                to: '/three',
            },
        ],
    }),
    updated() {

    },
    created() {
        this.drawer = this.$vuetify.breakpoint.mdAndUp

    },
    computed: {
        breakepointName() {
            return this.$vuetify.breakpoint.name
        },
    },
    methods: {
        logout() {
            this.$store.commit('logout')
            //this.$store.state.token = null
            this.$router.push('/login')
        },
        onResize() {
            this.windowSize = { x: window.innerWidth, y: window.innerHeight }            
        },
    },
    beforeDestroy() {
    
    },
    mounted() {

        this.onResize()
        // this.$store.dispatch('getIP').then(() => {
        //   //this.ip = this.$store.state.myIp      
        //   console.log(this.$store.state.myIp);
        //   this.ip = this.$store.state.myIp
        // });  
        this.$store.commit('increment', 3)
    },

}
</script>
<style lang="scss">
</style>
